import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Photos from '../views/Photos.vue'
import Venue from '../views/Venue.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Photos',
    name: 'Photos',
    component: Photos
  },
  {
    path: '/Venue',
    name: 'Venue',
    component: Venue
  }
  
]

const router = new VueRouter({
  mode: 'hash',
  base: '/wayne-rebecca-wedding',
  routes
})

export default router
